import React, { useState } from 'react';
import { View, Text, StyleSheet, Platform, useWindowDimensions } from 'react-native';
import type { NativeStackScreenProps } from '@react-navigation/native-stack';
import { useFoils } from '../hooks/useFoils';
import { useSearchFoils } from '../hooks/useSearchFoils';
import { useFilteredFoils } from '../hooks/useFilteredFoils';
import { useSortedFoils, SortField, SortOption } from '../hooks/useSortedFoils';
import { useComparison } from '../hooks/useComparison';
import { FoilsTable } from '../components/FoilsTable';
import ComparisonControls from '../components/ComparisonControls';
import ComparisonView from '../components/ComparisonView';
import Footer from '../components/Footer';
import { LoadingIndicator } from '../components/LoadingIndicator';
import { SEOHead } from '../components/SEOHead';
import { RootStackParamList } from '../types/navigation';
import { useTheme } from '../theme/ThemeContext';
import { BaseProduct } from '../types/scraper';
import { FilterOptions } from '../types/filters';
import { CONTAINER_PADDING, MOBILE_BREAKPOINT, Column } from '../constants/layout';

type Props = NativeStackScreenProps<RootStackParamList, 'Browse'> & {
  filters: {
    brands: string[];
    series: string[];
    type: string[];
  };
  onFilterChange: (type: keyof FilterOptions, value: string) => void;
  columns: Column[];
};

export default function BrowseScreen({ route, navigation, filters, onFilterChange, columns }: Props) {
  const { width } = useWindowDimensions();
  const isMobile = width < MOBILE_BREAKPOINT;
  const { foils, loading, error, refreshFoils } = useFoils();
  const [isComparisonView, setIsComparisonView] = useState(false);
  const { filteredFoils, filterOptions } = useFilteredFoils(foils, filters);
  const { colors } = useTheme();

  const { searchedFoils } = useSearchFoils(filteredFoils, route.params?.searchQuery || '');
  const { sortOption, setSortOption, sortedFoils } = useSortedFoils(searchedFoils);
  const { 
    selectedFoils,
    toggleFoilSelection,
    isSelected,
    clearSelection
  } = useComparison();

  const handleSort = (field: SortField) => {
    setSortOption(prev => ({
      field,
      direction: prev.field === field && prev.direction === 'asc' ? 'desc' : 'asc'
    }));
  };

  const handleCompareSelected = () => {
    setIsComparisonView(true);
  };

  const handleExitCompare = () => {
    setIsComparisonView(false);
  };

  if (loading) {
    return <LoadingIndicator />;
  }

  if (error) {
    return <Text style={[styles.error, { color: colors.error }]}>{error}</Text>;
  }

  return (
    <View style={[styles.container, { backgroundColor: colors.background.primary }]}>
      <SEOHead
        title="FoilQuiver - Browse and compare hydrofoils"
        description={`Compare ${foils.length} hydrofoils across ${filterOptions.brands.length} brands. Filter by type, brand, and specifications to find your perfect foil.`}
        canonicalUrl="https://foilquiver.com/browse"
      />
      <View style={[
        styles.content,
        !isMobile && styles.contentDesktop
      ]}>
        <View style={styles.tableContainer}>
          {isComparisonView ? (
            <ComparisonView 
              foils={selectedFoils} 
              onClose={handleExitCompare}
              onCompare={() => {}}
              columns={columns}
            />
          ) : (
            <FoilsTable
              foils={sortedFoils}
              onSort={handleSort}
              onFoilSelect={toggleFoilSelection}
              isSelected={isSelected}
              sortOption={{
                field: sortOption.field,
                ascending: sortOption.direction === 'asc'
              }}
              columns={columns}
              filters={filters}
            />
          )}
        </View>
        <ComparisonControls
          selectedFoils={selectedFoils}
          onCompareSelected={handleCompareSelected}
          onExitCompare={handleExitCompare}
          onClearSelection={clearSelection}
          isComparisonView={isComparisonView}
        />
      </View>
      <Footer />
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  content: {
    flex: 1,
    width: '100%',
    alignItems: 'flex-start',
  },
  contentDesktop: {
    paddingHorizontal: CONTAINER_PADDING,
  },
  tableContainer: {
    flex: 1,
    width: '100%',
  },
  error: {
    padding: 16,
    textAlign: 'center',
  },
}); 